<template>
  <gs-modal
    v-model="visible"
    :show-confirm="false"
    title="API"
    width="90%"
    top="60px"
    cancel-text=""
    @visible-change="visibleChange"
    @cancel="$emit('close')"
  >
    <div class="api-detail-page-modal">
      <div class="api-detail-body">
        <gs-loading :is-show="isDetailLoading" />
        <div class="api-base-info">
          <div class="info-text">
            <div class="api-name">
              {{ detailData.name || '-' }}
              <span
                :class="{'status-label': true, 'status-published': detailData.publish}"
              >{{ detailData.publish ? '' : '' }}</span>
            </div>
            <div class="info-item">
              <div class="info-item-text">
                {{ detailData.keeper || '-' }}
              </div>
              <div class="info-item-text">
                {{ detailData.generateTime || '-' }}
              </div>
              <div class="info-item-text">
                {{ detailData.updater || '-' }}
              </div>
              <div class="info-item-text">
                {{ detailData.updateTime || '-' }}
              </div>
            </div>
          </div>
        </div>
        <div class="api-extract-info">
          <gs-tabs
            v-model="activeTab"
            type="content-tab"
          >
            <gs-tab-pane
              label="API"
              name="info"
            >
              <div class="info-body">
                <div class="info-body-base">
                  <div class="info-body-base-title"></div>
                  <div class="info-body-base-item">
                    <div class="info-item-left">
                      <div class="info-item-left-text">
                        <span class="info-label">API</span>{{ detailData.name || '-' }}
                      </div>
                      <div class="info-item-left-text">
                        <span class="info-label">API</span>{{ detailData.groupName || '-' }}
                      </div>
                      <div class="info-item-left-text">
                        <span class="info-label"></span>{{ detailData.version || '-' }}
                      </div>
                      <div class="info-item-left-text">
                        <span class="info-label"></span>{{ detailData.description || '-' }}
                      </div>
                    </div>
                    <div class="info-item-right">
                      <div class="info-item-right-text">
                        <span class="info-label"></span>{{ detailData.protocol || '-' }}
                      </div>
                      <div class="info-item-right-text">
                        <span class="info-label"></span>{{ detailData.requestMode || '-' }}
                      </div>
                      <div class="info-item-right-text">
                        <span class="info-label"></span>{{ detailData.returnType || '-' }}
                      </div>
                      <div class="info-item-right-text">
                        <span class="info-label">API</span>{{ detailData.path || '-' }}
                      </div>
                    </div>
                  </div>
                </div>
                <div class="info-body-data">
                  <div class="info-body-data-title"></div>
                  <div class="info-body-data-item">
                    <div class="info-item-text">
                      {{ detailData.dbName || '-' }}
                    </div>
                    <div class="info-item-text">
                      {{ detailData.tableName || '-' }}
                    </div>
                    <div class="info-item-text">
                      Owner{{ dataOwners }}
                    </div>
                  </div>
                  <div class="info-body-data-table">
                    <el-table
                      :data="apiTableData"
                    >
                      <el-table-column
                        type="index"
                        label=""
                      />
                      <el-table-column
                        prop="columnName"
                        label=""
                      />
                      <el-table-column
                        prop="filter"
                        label=""
                      >
                        <template slot-scope="scope">
                          {{ scope.row.filter ? '' : '' }}
                        </template>
                      </el-table-column>
                      <el-table-column
                        prop="fill"
                        label=""
                      >
                        <template slot-scope="scope">
                          {{ scope.row.fill ? '' : '' }}
                        </template>
                      </el-table-column>
                      <el-table-column
                        prop="defaultValue"
                        label=""
                      />
                    </el-table>
                  </div>
                </div>
              </div>
            </gs-tab-pane>
            <gs-tab-pane
              label=""
              name="test"
            >

              <div class="info-body">
                <div class="req-msg">
                  <h5 class="title"></h5>
                  <div class="request">
                    <div
                      v-for="(item, index) in testData"
                      :key="index"
                      class="fields"
                    >
                      <gs-checkbox
                        v-model="item.check"
                        class="name"
                      >
                        <span
                          :title="item.columnName"
                        >{{ item.columnName }}</span>
                      </gs-checkbox>
                      <span class="value">
                        <gs-date-picker
                          v-if="item.type === 'timestamp'"
                          v-model="item.value"
                          type="date-time"
                        />
                        <gs-radio-group
                          v-else-if="item.type === 'boolean'"
                          v-model="item.value"
                        >
                          <gs-radio :label="true" />
                          <gs-radio :label="false" />
                        </gs-radio-group>
                        <gs-input-number
                          v-else-if="numberType.includes(item.type)"
                          v-model="item.value"
                        />
                        <gs-input
                          v-else
                          v-model="item.value"
                        />
                      </span>
                    </div>
                    <div class="fields">
                      <span class="name">limit</span>
                      <span class="value">
                        <gs-input-number
                          :precision="0"
                          v-model="limit"
                        />
                      </span>
                    </div>
                    <div class="fields">
                      <span class="name">offset</span>
                      <span class="value">
                        <gs-input-number
                          :precision="0"
                          v-model="offset"
                        />
                      </span>
                    </div>
                    <div class="gap" />
                  </div>
                  <div class="test-operation">
                    <gs-button
                      :disabled="!btnLoading"
                      @click="stopTest"
                    ></gs-button>
                    <gs-button
                      :loading="btnLoading"
                      type="primary"
                      @click="startTest"
                    ></gs-button>
                  </div>
                </div>
                <div class="rep-res">
                  <h5 class="title"></h5>
                  <div class="response">
                    <gs-tabs
                      v-model="activeName"
                      type="content-tab"
                    >
                      <gs-tab-pane
                        label=""
                        name="json"
                      >
                        <gs-textarea
                          :value="testResStringify"
                          :autosize="{
                            minRows: 8,
                            maxRows: 12
                          }"
                          readonly
                        />
                      </gs-tab-pane>
                      <gs-tab-pane
                        label=""
                        name="table"
                      >
                        <el-table
                          :data="testRes"
                        >
                          <el-table-column
                            v-for="(item, index) in tableHeader"
                            :key="index"
                            :label="item"
                            :prop="item"
                          />
                        </el-table>
                      </gs-tab-pane>
                    </gs-tabs>
                  </div>
                </div>
              </div>
            </gs-tab-pane>
          </gs-tabs>
        </div>
      </div>
    </div>
  </gs-modal>
</template>

<script>
import * as service from 'src/service/business-obj/business';
import axios from 'axios';
import './style';
let randomName = Math.random().toString(36).slice(-8);

export default {
  props: {
    id: {
      type: [String, Number],
      default: ''
    },
    show: {
      type: Boolean,
      default: false
    },
    module: {
      type: String,
      default: 'business-obj'
    }
  },
  data() {
    return {
      apiDetail: {},
      apiId: '',
      activeTab: 'info',
      testData: [],
      limit: 10,
      offset: 0,
      btnLoading: false,
      activeName: 'json',
      testRes: [],
      numberType: ['double', 'float', 'int', 'bigint', 'decimal'],
      isDetailLoading: false,
      visible: false
    };
  },
  computed: {
    detailData() {
      return this.apiDetail || {};
    },
    apiTableData() {
      return this.detailData.fields || [];
    },
    dataOwners() {
      const owners = this.detailData.dataOwner || [];
      return owners.join('') || '-';
    },
    tableHeader() {
      const {testRes} = this;
      if (testRes && testRes[0]) {
        return Object.keys(testRes[0]);
      } else {
        return [];
      }
    },
    testResStringify() {
      return JSON.stringify(this.testRes, null, 4);
    }
  },
  watch: {
    show(value) {
      this.visible = value;
    }
  },

  methods: {
    stopTest() {
      this.source.cancel();
      service.stopTestApi(randomName, this.module === 'business-management').then(data => {
        this.$Message.success(``);
      });
    },
    startTest() {
      if (this.checkTestInput()) {
        const {
          tableGuid,
          dbGuid,
          maxRowNumber
        } = this.detailData;
        const parameter = this.testData.filter(item => item.check)
          .map(item => {
            if (item.type === 'timestamp' && item.value) {
              return {columnName: item.columnName, value: [item.value.unix()]};
            }
            return {columnName: item.columnName, value: [item.value]};
          });
        const queryFields = this.detailData.fields.map(item => item.columnName);
        const params = {
          tableGuid,
          dbGuid,
          maxRowNumber,
          parameter,
          queryFields,
          limit: this.limit,
          offset: this.offset
        };
        randomName = Math.random().toString(36).slice(-8);
        this.btnLoading = true;
        const { CancelToken } = axios;
        this.source = CancelToken.source();
        service.testApi({params, randomName}, this.source.token, this.module === 'business-management')
          .then(data => {
            this.testRes = data;
            this.btnLoading = false;
          }).catch(e => {
            this.btnLoading = false;
            console.log('Request canceled', e.message);
          });
      }
    },
    checkTestInput() {
      const fill = this.testData.filter(item => item.fill && !item.check);
      if (fill && fill.length) {
        const str = fill.map(i => i.columnName).join(',');
        this.$Message.error(`${str}`);
        return false;
      }
      const checkStr = this.testData.filter(item => item.check)
        .filter(item => {
          if (
            (this.numberType.includes(item.type) && item.value === '') ||
          (item.type === 'timestamp' && item.value === '') ||
          (item.type === 'boolean' && item.value === '')
          ) {
            return true;
          } else {
            return false;
          }
        })
        .map(i => i.columnName).join(',');
      if (checkStr) {
        this.$Message.error(`${checkStr}`);
        return false;
      }
      return true;
    },
    visibleChange(val) {
      if (val && this.id) {
        this.isDetailLoading = true;
        service.getApiDetailInfo(this.id, this.module === 'business-management').then(data => {
          this.apiDetail = data;
          this.isDetailLoading = false;
          this.testData = (data.fields || [])
            .filter(item => item.filter)
            .map(item => {
              return {
                columnName: item.columnName,
                value: '',
                check: true,
                type: item.type,
                fill: item.fill
              };
            });
        });
      }
    }
  }
};
</script>



